import logo from './logo.svg';
import './App.css';
import React, { useState, useEffect } from 'react';
import axios from 'axios';
import TinyFlag from 'tiny-flag-react';

function App() {

  const [songs, setSongs] = useState([]);

  useEffect(() => {
    axios({
      method: "GET",
      url: 'http://127.0.0.1:8000/api/test-api/'
    }).then(response => {
      setSongs(response.data)
    })
    }, [])

  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <ul>
          {songs.map(song => (
            <li key={song.id}>
              {song.country}
              <TinyFlag country='RU' alt='Russian flag' fallbackImageURL='https://cdn.jsdelivr.net/npm/react-flagkit@1.0.2/img/SVG/RU.svg'/>
            </li>
          ))} 
        </ul>
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React
        </a>
      </header>
    </div>
  );
}

export default App;
